.reveal-modal-bg {
  position: fixed;
  height: 100%;
  width: 100%;
  background: black;
  background: rgba(0, 0, 0, 0.45);
  z-index: 98;
  display: none;
  top: 0;
  left: 0; }

.reveal-modal .modal-title {
  padding: 10px;
  font-size: 16px;
  min-height: 40px;
  margin: 0;
  background: #555;
  color: #fff;
  font-weight: 400; }
  .reveal-modal .modal-title .large-3 {
    padding: 0; }

.reveal-modal .wrapper {
  padding: 12px 5px 0; }

.reveal-modal .close-reveal-modal {
  font-size: 34px;
  line-height: 25px;
  position: absolute;
  top: 0.23em;
  right: 10px;
  color: #fff;
  font-weight: bold;
  cursor: pointer; }
  .reveal-modal .close-reveal-modal:hover {
    text-decoration: none; }

.reveal-modal .footer {
  text-align: center;
  padding-top: 16px;
  padding-bottom: 16px; }
  .reveal-modal .footer .button {
    margin-bottom: 0;
    font-size: 16px;
    padding: 10px 40px; }
