.add-menu-control {
  position: relative; }
  .add-menu-control button {
    margin: 0;
    padding: 10px 15px; }
  .add-menu-control .menu-width {
    width: 100px; }
  .add-menu-control .menu-item {
    background: white;
    border: 1px solid black;
    border-top: 0;
    text-align: center; }
    .add-menu-control .menu-item:hover {
      cursor: pointer;
      background: darkgrey; }
  .add-menu-control .menu-items {
    position: absolute;
    right: 0; }
