[kanban-column] {
  display: table-cell;
  vertical-align: top;
  width: 228px;
  min-width: 228px;
  background: #ededed;
  border-right: 1px solid #DBDCDE;
  box-sizing: border-box;
  position: relative;
  height: 100%;
  padding-top: 8px; }
  [kanban-column]:after {
    content: '';
    display: table-cell;
    width: 100%;
    height: 100px; }
  [kanban-column].kanban-clmn-collapsed {
    width: 30px;
    min-width: 30px;
    border: 1px dashed #ccc; }
    [kanban-column].kanban-clmn-collapsed .kanban-card {
      display: none; }
    [kanban-column].kanban-clmn-collapsed .kanban-clmn-warning {
      display: none !important; }
  [kanban-column] .kanban-clmn-warning {
    display: none;
    text-align: center;
    font-size: 18px;
    font-weight: 600;
    position: absolute;
    top: 100px;
    width: 80%;
    left: 10%;
    right: 10%;
    z-index: 1;
    color: #555555; }
    [kanban-column] .kanban-clmn-warning glyph-icon {
      font-size: 24px;
      display: block;
      margin-bottom: 14px; }
  [kanban-column] .as-sortable-placeholder {
    background: #f9f9f9;
    border: 1px #b0b0b0 dashed;
    border-radius: 3px;
    margin: 5px auto; }
  [kanban-column] .select-skills-button {
    min-width: 230px;
    border-width: 1px; }

.swim-lane.empty [kanban-column] {
  display: none; }
