scrollbar {
  display: block;
  overflow-x: auto;
  overflow-y: hidden;
  height: 20px; }
  scrollbar .scroll-bar {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 20px; }
    scrollbar .scroll-bar .clmn {
      width: 228px;
      min-width: 228px;
      display: table-cell; }
    scrollbar .scroll-bar .clmn-collapsed {
      width: 30px;
      min-width: 30px;
      max-width: 30px; }
