.task-group-modal {
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  padding: 0;
  position: fixed;
  top: 25% !important;
  left: 0;
  right: 0;
  transform: translateY(-25%);
  -ms-transform: translateY(-25%);
  z-index: 99;
  background-color: white;
  border: solid 1px #555;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  font-size: 14px;
  margin: 0 auto !important;
  width: 100% !important;
  max-width: 500px;
  outline: none; }
  .task-group-modal .header {
    display: table;
    table-layout: fixed;
    width: 100%;
    min-height: 40px; }
    .task-group-modal .header .group-icon {
      font-size: 26px;
      width: 40px;
      text-align: left;
      vertical-align: middle;
      display: table-cell; }
    .task-group-modal .header .group-name {
      display: table-cell;
      vertical-align: middle;
      font-size: 16px;
      font-weight: 600;
      padding-right: 12px; }
  .task-group-modal .info .info-label {
    font-weight: 600;
    margin-bottom: -4px; }
  .task-group-modal .info .info-label-very-bold {
    font-weight: bold; }
  .task-group-modal .info .group-info {
    margin-bottom: 10px;
    display: inline-block;
    width: 201px;
    vertical-align: top;
    font-weight: 300; }
    .task-group-modal .info .group-info.secondary {
      color: #AEACAD; }
  .task-group-modal .info .status {
    color: #ff331d; }
    .task-group-modal .info .status.complete {
      color: #41903D; }
  .task-group-modal .legend {
    color: #aaa7a7;
    font-weight: 600;
    position: relative;
    font-size: 12px; }
    .task-group-modal .legend span {
      background: #fff;
      padding-right: 10px; }
    .task-group-modal .legend:after {
      content: "\00a0";
      position: absolute;
      border-bottom: 1px solid #e7e7e7;
      left: 0;
      right: 0;
      top: -50%;
      z-index: -1; }
  .task-group-modal .delete-button {
    color: #31b3ff;
    vertical-align: middle;
    display: inline-block;
    line-height: 16px;
    font-weight: 400;
    padding: 5px 0;
    margin: 0;
    margin-bottom: 4px;
    font-size: 14px; }
    .task-group-modal .delete-button .tollbar-icon {
      font-size: 16px;
      margin-right: 5px; }
    .task-group-modal .delete-button span {
      vertical-align: middle; }
