.as-sortable-un-selectable .swim-lane .card-highlight {
  opacity: 0.3; }

.swim-lane {
  z-index: 2; }
  .swim-lane .swim-title {
    margin-left: 6px;
    font-size: 18px;
    line-height: 27px;
    vertical-align: middle;
    color: #555555;
    font-weight: 600; }
  .swim-lane .swim-sub-title {
    margin-left: 6px;
    font-size: 13px;
    font-style: italic;
    line-height: 27px;
    vertical-align: middle;
    color: #555555;
    font-weight: 400; }
  .swim-lane .swim-toolbar {
    display: inline-block;
    text-align: right;
    width: 100%; }
  .swim-lane .swim-button {
    color: #31b3ff;
    font-size: 18px;
    margin: 0;
    float: right; }
  .swim-lane .swim-lane-header {
    background: #ffffff;
    padding: 10px 20px;
    border: 1px solid #cbcbcb;
    height: 50px;
    margin: 0 -8px; }
  .swim-lane .swim-lane-toggle {
    cursor: pointer;
    color: #555555;
    font-weight: 600; }
  .swim-lane .swim-length, .swim-lane .swim-loading {
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    vertical-align: middle; }
  .swim-lane .swim-loading {
    color: #ccc; }
  .swim-lane .swim-lane-header:hover .edit-tasks-button {
    visibility: visible; }
  .swim-lane .edit-tasks-button {
    float: right;
    visibility: hidden; }
    .swim-lane .edit-tasks-button .button {
      margin: 0;
      padding: 0;
      font-size: 16px;
      background: transparent;
      border: none;
      line-height: 28px;
      font-weight: 400;
      text-decoration: underline;
      color: #ccc; }
      .swim-lane .edit-tasks-button .button:hover {
        color: #31b3ff; }
  .swim-lane.edit .edit-tasks-button {
    visibility: visible; }
  .swim-lane.empty .kanban-column {
    border-right: none; }
  .swim-lane.no-groups .add-resource-button {
    color: #AAAAAA; }
    .swim-lane.no-groups .add-resource-button .button {
      color: #AAAAAA; }
  .swim-lane .add-resource-button {
    color: #31b3ff;
    float: right; }
    .swim-lane .add-resource-button .button {
      color: #31b3ff;
      margin: 0;
      padding: 0;
      font-size: 16px;
      background: transparent;
      border: none;
      line-height: 28px;
      font-weight: 600; }
      .swim-lane .add-resource-button .button:hover {
        text-decoration: underline; }
    .swim-lane .add-resource-button .glyph-icon {
      font-size: 12px;
      margin-right: 5px;
      position: relative;
      top: -3px; }
  .swim-lane.no-groups .add-resource-button {
    cursor: default; }
    .swim-lane.no-groups .add-resource-button .button {
      cursor: default; }
    .swim-lane.no-groups .add-resource-button .button:hover {
      text-decoration: none; }

.kanban-read-only .swim-lane-toggle {
  cursor: initial;
  padding: 10px; }

@media only screen and (max-width: 768px) {
  .swim-lane .swim-lane-header {
    margin: 0; } }
