.column-names {
  display: table;
  table-layout: fixed;
  height: 35px; }
  .column-names .tooltip-area {
    height: 60px;
    position: absolute;
    bottom: 0;
    width: 30px;
    z-index: 5; }
  .column-names .column-name {
    color: #A0A2A1;
    text-align: center;
    background: #ffffff;
    width: 228px;
    min-width: 228px;
    border-right: 1px solid #DBDCDE;
    display: table-cell;
    padding: 6px 0 10px 0;
    cursor: pointer;
    vertical-align: middle; }
    .column-names .column-name:last-child {
      float: none; }
    .column-names .column-name:hover .column-toggle-icon {
      color: #31B3FF; }
  .column-names .title {
    font-size: 14px;
    font-weight: 600;
    text-transform: uppercase;
    padding: 0 4px;
    word-wrap: break-word;
    line-height: 1;
    display: block; }
  .column-names .column-name-collapsed {
    width: 30px;
    min-width: 30px;
    text-overflow: ellipsis;
    max-width: 30px;
    background: #ededed;
    position: relative;
    border: 1px dashed #ccc;
    height: 60px;
    vertical-align: top;
    padding-top: 10px; }
    .column-names .column-name-collapsed .title {
      white-space: nowrap;
      display: block;
      height: 28px;
      overflow: hidden;
      background: #ededed;
      transform-origin: left bottom 0;
      text-align: left;
      z-index: 2;
      position: absolute;
      top: 0;
      padding: 7px 0;
      text-overflow: ellipsis;
      width: 30px;
      transform: rotate(90deg); }
    .column-names .column-name-collapsed:hover .title, .column-names .column-name-collapsed:hover .column-toggle-icon {
      color: #31B3FF; }
  .column-names .column-toggle-icon {
    display: block;
    font-size: 16px;
    cursor: pointer;
    color: #cccccc;
    overflow: hidden;
    margin-top: -4px; }
    .column-names .column-toggle-icon .glyph-icon {
      transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      width: 20px;
      height: 20px;
      display: block;
      margin: 0 auto; }

.tooltip {
  background: #ededed;
  color: #A0A2A1;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 14px;
  font-weight: 500; }
  .tooltip .nub {
    border-color: transparent transparent #ededed transparent; }

.lt-ie9 .column-names .column-name-collapsed .title {
  line-height: 28px;
  top: 20px;
  height: 50px;
  writing-mode: tb-rl; }
