.reveal-modal.task-card-modal {
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  padding: 0;
  position: fixed;
  top: 25% !important;
  left: 0;
  right: 0;
  transform: translateY(-25%);
  -ms-transform: translateY(-25%);
  z-index: 99;
  background-color: white;
  border: solid 1px #555;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  font-size: 14px;
  margin: 0 auto !important;
  width: 100% !important;
  max-width: 500px;
  outline: none; }
  .reveal-modal.task-card-modal .card-icon {
    font-size: 24px; }
  .reveal-modal.task-card-modal .task-field {
    font-size: 14px;
    margin: 6px 0;
    font-weight: 400;
    line-height: 1; }
  .reveal-modal.task-card-modal .legend {
    color: #aaa7a7;
    font-weight: 400;
    position: relative;
    font-size: 12px;
    margin-bottom: 5px; }
    .reveal-modal.task-card-modal .legend span {
      background: #fff;
      padding-right: 10px; }
  .reveal-modal.task-card-modal .legend:after {
    content: "\00a0";
    position: absolute;
    border-bottom: 1px solid #e7e7e7;
    left: 0;
    right: 0;
    top: -50%;
    z-index: -1; }
  .reveal-modal.task-card-modal .header {
    display: table;
    table-layout: fixed;
    width: 100%;
    min-height: 40px; }
  .reveal-modal.task-card-modal .title {
    display: table-cell;
    vertical-align: middle;
    font-size: 14px;
    font-weight: 600;
    padding-right: 8px; }
  .reveal-modal.task-card-modal .app-tag {
    width: 31px;
    text-align: left;
    vertical-align: middle;
    display: table-cell; }
  .reveal-modal.task-card-modal .name {
    font-size: 16px;
    margin-bottom: 16px;
    margin-top: 10px;
    font-weight: 600; }
  .reveal-modal.task-card-modal .description {
    font-size: 14px;
    margin: 6px 0 12px 0; }
  .reveal-modal.task-card-modal .tollbar-icon {
    font-size: 16px;
    margin-right: 5px; }
  .reveal-modal.task-card-modal .toolbar-button {
    color: #31b3ff;
    vertical-align: middle;
    display: inline-block;
    line-height: 16px;
    font-weight: 400;
    padding: 0;
    margin: 0 25px 16px 0;
    font-size: 14px; }
    .reveal-modal.task-card-modal .toolbar-button span {
      vertical-align: middle; }
  .reveal-modal.task-card-modal .priority-level {
    font-size: 14px;
    font-weight: 600; }
  .reveal-modal.task-card-modal .star-rating li {
    font-size: 18px; }
  .reveal-modal.task-card-modal .field-label {
    font-weight: 600; }
  .reveal-modal.task-card-modal .link-text {
    vertical-align: middle; }
  .reveal-modal.task-card-modal .link-icon {
    font-size: 16px;
    vertical-align: middle;
    display: inline-block;
    margin-left: 6px;
    margin-top: -10px;
    position: relative;
    top: 4px; }
  .reveal-modal.task-card-modal .modal-divider {
    margin-top: 0;
    margin-bottom: 6px; }
  .reveal-modal.task-card-modal .assignees {
    font-weight: bold; }
