.task-group-list {
  background: white;
  position: relative;
  overflow: hidden;
  padding-bottom: 20px;
  border-top: 1px solid #DBDCDE;
  box-sizing: content-box; }
  .task-group-list .white-line-wrapper {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0; }
  .task-group-list .white-line {
    height: 100%;
    width: 1px;
    background: #DBDCDE;
    display: inline-block;
    margin-left: 227px; }
  .task-group-list .white-line:last-child {
    display: none; }
  .task-group-list .task-group-empty {
    padding: 15px;
    width: 100%;
    text-align: center;
    max-width: 1115px;
    border: 2px dashed #a6a6a6;
    margin: 20px 10px 10px;
    z-index: 1;
    position: relative; }
    .task-group-list .task-group-empty p {
      font-style: italic;
      color: #a5a5a5;
      font-size: 16px;
      line-height: 1.2; }
