.task-group {
  display: inline-block;
  vertical-align: top;
  box-sizing: border-box;
  padding: 2px 5px; }
  .task-group .unbold {
    font-weight: normal; }
  .task-group.transiton {
    transition: all 0.5s ease; }
  .task-group.draggy {
    position: absolute;
    display: block; }
  .task-group.expanded-group .task-group-content {
    background: white !important; }
  .task-group.expanded-group .info {
    display: block; }
  .task-group.expanded-group .drag-button {
    background: #763597;
    color: white;
    padding-top: 10px;
    padding-left: 1px; }
  .task-group.expanded-group .card-icon {
    display: inline-block; }
  .task-group.expanded-group .resize-button {
    margin-top: 2px; }
  .task-group.expanded-group .group-name {
    margin-top: 2px;
    margin-bottom: 7px; }
  .task-group.expanded-group .after-button-area {
    margin-top: 6px;
    margin-left: 19px;
    margin-bottom: 7px;
    display: inline-block; }
  .task-group .after-button-area {
    margin-left: 6px;
    cursor: pointer; }
  .task-group .task-group-color {
    position: absolute;
    top: 0;
    height: 100%;
    background-color: black;
    width: 5px;
    left: 0;
    bottom: 0; }
  .task-group .task-group-content {
    cursor: pointer;
    transition: 500ms cubic-bezier(0.25, 0.25, 0.75, 0.75) all;
    padding: 2px 2px 6px 4px;
    background: #f6f6f6;
    position: relative;
    color: #5D5D5D;
    font-weight: bold;
    font-size: 14px;
    border: 1px solid #efefef; }
    .task-group .task-group-content.opacity {
      background: #ffffff !important; }
    .task-group .task-group-content.opacity > * {
      opacity: 0; }
  .task-group .info {
    display: none;
    margin-left: 11px; }
    .task-group .info .group-info {
      display: inline-block;
      width: 201px;
      vertical-align: top;
      font-weight: 300; }
      .task-group .info .group-info.secondary {
        color: #AEACAD; }
      .task-group .info .group-info.status {
        color: #ff331d; }
        .task-group .info .group-info.status.complete {
          color: #41903D; }
  .task-group .card-icon {
    display: none; }
  .task-group .group-name {
    margin-left: 15px;
    margin-top: 6px;
    margin-bottom: 3px;
    display: inline-block;
    width: 77%;
    white-space: nowrap;
    overflow: hidden !important;
    text-overflow: ellipsis; }
  .task-group .resize-button {
    color: #C1C3C5;
    z-index: 90;
    display: inline-block;
    cursor: col-resize;
    position: absolute;
    top: 6px;
    bottom: 2px;
    right: 9px; }
  .task-group .drag-button {
    color: #C1C3C5;
    position: absolute;
    top: 0;
    bottom: 0;
    width: 20px;
    padding-top: 7px;
    cursor: move; }
  .task-group.highlighted-group .group-name, .task-group.highlighted-group .resize-button, .task-group.highlighted-group .drag-button {
    color: #ffffff; }

.kanban-read-only .task-group .after-button-area {
  margin-left: 0; }
