.add-dropdown {
  float: right;
  position: relative;
  min-width: 170px; }
  .add-dropdown .add-dropdown-list {
    background: #ffffff;
    position: absolute;
    width: 100%;
    border: 2px solid #C5C6C8;
    z-index: 100; }
  .add-dropdown .add-dropdown-item {
    padding: 5px 20px;
    margin: 5px 0;
    font-size: 14px;
    font-weight: 600; }
    .add-dropdown .add-dropdown-item a {
      color: #555555; }
  .add-dropdown .button {
    width: 100%;
    text-align: left;
    border: 1px solid #AFC4D5;
    color: #fff;
    background: #31b3ff; }
    .add-dropdown .button .glyph-icon {
      float: right;
      font-size: 12px;
      font-weight: 700; }
    .add-dropdown .button.expanded {
      color: #fff;
      background: #31b3ff; }
      .add-dropdown .button.expanded .glyph-chevron-expanded {
        display: none; }
      .add-dropdown .button.expanded .glyph-chevron-collapsed {
        display: block; }
    .add-dropdown .button .glyph-chevron-collapsed {
      display: none; }
  .add-dropdown.no-groups .button {
    background: #AAAAAA; }

add-dropdown {
  display: block; }

.kanban-read-only add-dropdown {
  display: none; }
